import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import Box from 'common/src/components/Box';
import Text from 'common/src/components/Text';
import Image from 'common/src/components/Image';
import Heading from 'common/src/components/Heading';
import Button from 'common/src/components/Button';
import FeatureBlock from 'common/src/components/FeatureBlock';
import Container from 'common/src/components/UI/Container';
import Particles from '../Particle';
import BannerWrapper, {
  BannerObject,
} from './bannerSection.style';

import BannerObject2 from 'common/src/assets/image/saas/banner/bannerObject2.png';

const BannerSection = ({
  row,
  col,
  colFull,
  colImage,
  title,
  title2,
  btnStyle,
  description,
  discountText,
  discountAmount,
  outlineBtnStyle,
}) => {
  const ButtonGroup = () => (
    <Fragment>
      {/* <Button title="FREE TRAIL" {...btnStyle} /> */}
      <Button
        className="outlined"
        title="EXPLORE MORE"
        // variant="outlined"
        {...btnStyle}
        onClick={() => (window.location.href = '#service_section')}
      />
    </Fragment>
  );


  return (
    <>
      <BannerWrapper id="banner_section" style={{ paddingTop: 80 }}>
        <Particles />
        <Container fullWidth>
          <Box className="row" {...row}>
            <Box className="col" {...col}>
              <FeatureBlock
                title={
                  <>
                  <Heading
                    content="Introducing Rapydly's Dynamic Back Office."
                    {...title}
                  />
                  <Heading
                    content="Deploy once, manage all."
                    {...title}
                    fontSize={30}
                    fontWeight={200}
                  />
                  </>
                }
                button={<ButtonGroup />}
              />
            </Box>
            <Box className="col" {...colImage}>
              <BannerObject>
                <Image
                  src={BannerObject2}
                  style={{ maxHeight: 500, margin: 'auto' }}
                  alt="BannerObject1"
                />
              </BannerObject>
            </Box>
          </Box>
        </Container>
      </BannerWrapper>

      <BannerWrapper id="service_section">
        <Container>
          <Box className="row" {...row}>
            <Box className="col" {...colFull}>
              {/* <DiscountLabel>
              <Text content="25% Discount" {...discountAmount} />
              <Text content="on every first project " {...discountText} />
            </DiscountLabel> */}
              <FeatureBlock
                title={
                  <>
                    {/* {NewBanner}  */}
                    <Heading
                      content={
                        <>
                          {"Manage all your data, for all your applications. With one back office, and only ever one deploy."}
                        </>
                      }
                      {...title2}
                      textAlign={'center'}
                    />
                  </>
                }
                description={
                  <Text
                    content={
                      <>
                        {
                          'A highly customisable, scalable, and responsive web interface for managing and visualising all of your data from databases. Rapydly\'s Dymanic Back Office builds itself from your database structure on the fly, meaning you\'ll never have to maintain a separate back office when other applications or databases evolve.'
                        }
                      </>
                    }
                    {...description}
                  />
                }
              />
            </Box>
          </Box>
        </Container>
      </BannerWrapper>
    </>
  );
};

BannerSection.propTypes = {
  title: PropTypes.object,
  btnStyle: PropTypes.object,
  description: PropTypes.object,
  contentStyle: PropTypes.object,
  discountText: PropTypes.object,
  discountAmount: PropTypes.object,
  outlineBtnStyle: PropTypes.object,
};

BannerSection.defaultProps = {
  row: {
    maxWidth: 1600,
    flexBox: true,
    flexWrap: 'wrap',
    ml: 'auto',
    mr: 'auto',
    alignItems: 'center',
  },
  col: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, '40%'],
  },
  colImage: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, '60%'],
  },
  colFull: {
    pr: '15px',
    pl: '15px',
    width: [1, 1, '80%'],
    margin: 'auto',
  },
  title: {
    fontSize: ['34px', '30px', '48px'],
    fontWeight: '600',
    color: '#0f2137',
    letterSpacing: '-0.025em',
    lineHeight: '1.5',
  },
  title2: {
    fontSize: ['29px', '25px', '43px'],
    fontWeight: '500',
    color: '#0f2137',
    letterSpacing: '-0.025em',
    lineHeight: '1.5',
    textAlign: ['left', 'center'],
  },
  description: {
    fontSize: '18px',
    color: '#343d48',
    mb: '0',
    textAlign: 'center',
    paddingLeft: [0, 0, 50],
    paddingRight: [0, 0, 50],
  },
  btnStyle: {
    minWidth: ['120px', '120px', '120px', '156px'],
    fontSize: ['13px', '14px'],
    fontWeight: '500',
    colors: 'primaryWithBg',
  },
  outlineBtnStyle: {
    minWidth: '156px',
    fontSize: '14px',
    fontWeight: '500',
    color: '#50b148',
    // ml: '18px',
  },
  discountAmount: {
    fontSize: '14px',
    color: '#50b148',
    mb: 0,
    as: 'span',
    mr: '0.4em',
    fontWeight: 700,
  },
  discountText: {
    fontSize: ['13px', '14px'],
    color: '#0f2137',
    mb: 0,
    as: 'span',
    fontWeight: 500,
  },
};

export default BannerSection;
